// eslint-disable-next-line @typescript-eslint/ban-ts-comment
//@ts-ignore
import BmfSteuerRechner2024 from "./2024bmfSteuerRechner";
import { InputParams } from "../../../defaultOptions";
import { BMFCalculator } from "./calculate";

interface TaxInputParams {
  inputParams: InputParams;
  taxBasis: number;
}

export interface Taxes {
  total: number;
  church: number;
  income: number;
  soli: number;
  b: BMFCalculator;
}

function getTaxes({ inputParams, taxBasis }: TaxInputParams) {
  const b = new BmfSteuerRechner2024();
  b.initParams();

  // Parameter für Steuerrechner
  b.STKL = inputParams.taxClass;
  b.LZZ = 2;
  b.ZKF = inputParams.childAllowances;
  b.AF = b.STKL === 5 ? 1 : 0;
  b.F = b.STKL === 5 ? inputParams?.taxFactor : 1;
  b.R = inputParams.churchTax === true ? 1 : 0;
  b.RE4 = taxBasis * 100;
  if (b.STKL > 4) {
    b.STKL -= 1;
  }

  b.PVZ = inputParams.childAllowances === 0 ? 1 : 0;
  b.KVZ = inputParams.healthInsuranceFactor;
  b.PKPV = 0; //IRRELEVANT: t.$selectKrankenkasse.val() == 1 ? 0 : parseFloat(t.$inputPrivKrankenkassenBeitrag.val());
  const currentYear = new Date().getFullYear();
  // Vorausgesetzt wird, dass kein Rentenalter besteht
  b.AJAHR = currentYear + 65;
  b.ALTER1 = 0;
  if (inputParams.workTypeOfficial) {
    b.PKV = 1;
    b.KRV = 2;
  } else {
    b.PKV = (inputParams?.healthInsurance || 1) - 1;
    b.KRV = inputParams?.pensionInsurance;
  }
  b.PVS = inputParams.region === "SACHSEN" ? 1 : 0;

  b.func_LST();

  const income = Math.floor(b.LSTLZZ) / 100;
  const soli = Math.floor(b.SOLZLZZ) / 100;

  const churchTaxFactor =
    inputParams.region === "BW" || inputParams.region === "BAYERN"
      ? 0.08
      : 0.09;
  const church =
    inputParams.churchTax === true
      ? Math.floor(b.BK * churchTaxFactor) / 100
      : 0;

  const total = income + soli + church;
  const outputParams: Taxes = {
    total,
    income,
    soli,
    church,
    b,
  };
  return outputParams;
}

export default getTaxes;
