interface BoxProps {
  children?: React.ReactNode;
  className?: string;
}

const Box = ({ children, className }: BoxProps) => {
  return (
    <div
      className={`flex gap-1 w-full items-center grow ${
        className || ""
      } justify-start md:justify-start`}
    >
      {children}
    </div>
  );
};

export default Box;
