import { useState } from "react";
import Input from "./Input";
import Button from "./Button";
import _t from "../../utils/translations";
// import Hint from "./Hint";

export interface Row {
  price: number;
  uvp: number;
  uid: number;
}

interface BikeInputRowProps {
  onChange: (params: { index: number; row: Row }) => void;
  onRemove: (index: number) => void;
  index: number;
  price: number;
  uvp: number;
  uid: number;
  removeAvailable: boolean;
  isProduct: boolean;
  maxBikePrice: number;
  minBikePrice: number;
  extended: boolean;
}

const BikeInputRow = ({
  onChange,
  onRemove,
  index,
  price = 0,
  uvp = 0,
  uid = 0,
  removeAvailable,
  isProduct,
  maxBikePrice,
  minBikePrice,
  extended,
}: BikeInputRowProps) => {
  const [value, setValue] = useState(price);
  const [valueUvp, setValueUvp] = useState(uvp);

  const handleChange = (newValue: number) => {
    if (isNaN(newValue)) newValue = 0;
    setValue(newValue);
    onChange({ index, row: { price: newValue, uvp: valueUvp, uid } });
  };

  const handleChangeUvp = (newValue: number) => {
    if (isNaN(newValue)) newValue = 0;
    setValueUvp(newValue);
    onChange({ index, row: { price: value, uvp: newValue, uid } });
  };

  return extended ? (
    <>
      <div
        className={`col-span-2 max-md:col-span-1 border-b border-b-[--color-border] pt-4 pb-2 mb-2 text-sm flex justify-between ${
          removeAvailable ? "" : "hidden"
        }`}
      >
        # {index + 1}
        <div>
          <div
            className={`flex justify-end items-center gap-2 text-xs cursor-pointer`}
            onClick={() => onRemove(index)}
          >
            <span className="opacity-30">{_t("removeBike")}</span>
            <Button
              variant="primary"
              noPadding
              className="rounded-none aspect-square w-5 leading-none text-xs"
            >
              -
            </Button>
          </div>
        </div>
      </div>

      {extended && (
        <div className="whitespace-normal text-lg">
          {_t("totalPrice")}
          {/* {isProduct && (
            <Hint>
              <h2>{_t("productPriceHintHeadline")}</h2>
              <p>{_t("productPriceHintText")}</p>
            </Hint>
          )} */}
        </div>
      )}
      <div className="pb-4">
        <Input
          type="number"
          prefix="€"
          min={minBikePrice}
          max={maxBikePrice}
          placeholder={price.toString()}
          initialValue={price}
          onChange={handleChange}
          disabled={isProduct}
          variant="big"
        />
      </div>

      {extended && <div className="text-lg">{_t("totalPriceUVP")}</div>}
      <div>
        <Input
          type="number"
          min={minBikePrice}
          max={maxBikePrice}
          prefix="€"
          placeholder={valueUvp.toString()}
          initialValue={valueUvp}
          disabled={isProduct}
          onChange={handleChangeUvp}
          variant="big"
        />
      </div>
    </>
  ) : (
    <div className="pb-5">
      <div>
        <Input
          type="number"
          min={minBikePrice}
          max={maxBikePrice}
          prefix="€"
          placeholder={valueUvp.toString()}
          initialValue={valueUvp}
          disabled={isProduct}
          onChange={(e: number) => {
            handleChangeUvp(e);
            handleChange(e);
          }}
        />
      </div>
    </div>
  );
};

export default BikeInputRow;
