/*!
 * Merge two or more objects together.
 * (c) 2017 Chris Ferdinandi, MIT License, https://gomakethings.com
 * @param   {Boolean}  deep     If true, do a deep (or recursive) merge [optional]
 * @param   {Object}   objects  The objects to merge together
 * @returns {Object}            Merged values of defaults and options
 *
 * Use the function as follows:
 * let shallowMerge = extend(obj1, obj2);
 * let deepMerge = extend(true, obj1, obj2)
 */

export interface OptionsObject {
  [key: string]: OptionsObject;
}

export const mergeOptions = (target: OptionsObject, source: OptionsObject) => {
  // Iterate through `source` properties and if an `Object` set property to merge of `target` and `source` properties
  for (const key of Object.keys(source)) {
    if (source[key] instanceof Object) {
      if (source[key].length) {
        target[key] = source[key];
      } else {
        Object.assign(source[key], mergeOptions(target[key], source[key]));
      }
    }
  }

  // Join `target` and modified `source`
  Object.assign(target || {}, source);
  return target as object;
};
