interface SavingsInput {
  bikePrices: {
    bikesPlusRepair: number;
    leasingRate: number;
    leasingRateNormalTotal: number;
  };
  totalCost: number;
  leasingRate: number;
  leasingPriceNormalTotal: number;
  leasingTotal: number;
}

export interface SavingsOutput {
  savingsPercent: number;
  savingsAbsolute: number;
  savingsLeasingPercent: number;
  savingsLeasingAbsolute: number;
}

function getSavings({
  bikePrices,
  totalCost,
  leasingRate,
  leasingPriceNormalTotal,
  leasingTotal,
}: SavingsInput) {
  // Berechnung der Ersparnis
  const savingsAbsolute = bikePrices.bikesPlusRepair - totalCost;
  const savingsPercent =
    Math.round(
      (1 - totalCost / bikePrices.bikesPlusRepair) *
        100 * // percent in 2-3 digits
        100 // for rounding
    ) / Math.pow(10, 2);

  const savingsLeasingPercent =
    ((bikePrices.leasingRate - leasingRate) / bikePrices.leasingRate) * 100;

  const savingsLeasingAbsolute = leasingPriceNormalTotal - leasingTotal;

  const output: SavingsOutput = {
    savingsPercent,
    savingsAbsolute,
    savingsLeasingPercent,
    savingsLeasingAbsolute,
  };
  return output;
}

export default getSavings;
