import React, { createContext, useContext, useState } from "react";
import Button from "./Button";

interface HintContextType {
  content: React.ReactNode | null;
  setContent: React.Dispatch<
    React.SetStateAction<React.ReactNode | null>
  > | null;
}

export const HintContext = createContext({
  content: null,
  setContent: null,
} as HintContextType);

export const useHintContext = () => {
  return useContext(HintContext);
};

export default function HintContextProvider({
  children,
}: {
  children: React.ReactNode[] | React.ReactNode;
}) {
  const [content, setContent] = useState(null);
  const handleClose = () => {
    setContent(null);
  };
  return (
    <HintContext.Provider value={{ content, setContent } as HintContextType}>
      <div className="z-10 relative">{children}</div>
      <div
        className={`fixed left-0 top-0 w-full h-full transition-all duration-200 ease-out bg-black/80 z-20 ${
          content
            ? "opacity-100 pointer-events-auto"
            : "opacity-0 pointer-events-none"
        }`}
        onClick={(e) => {
          handleClose();
          e.stopPropagation();
        }}
      >
        <div
          className={`transition-all duration-200 ease-out bg-white p-2 lg:p-8 rounded-md absolute left-1/2 top-1/2 right-auto bottom-auto max-h-[calc(100vh-50px)] w-[700px] max-w-[calc(100vw-20px)] flex overflow-auto ${
            content
              ? "-translate-y-1/2 -translate-x-1/2"
              : "-translate-y-1/2 -translate-x-1/2"
          }`}
          onClick={(e) => {
            e.stopPropagation();
          }}
        >
          <Button
            className={`absolute font-display top-0 right-0 rounded-full h-10 w-10 flex justify-center items-center bg-transparent text-black`}
            onClick={handleClose}
          >
            ✕
          </Button>
          <div className="w-full">{content}</div>
        </div>
      </div>
    </HintContext.Provider>
  );
}
