import { useEffect, useState } from "react";
import NonCashBenefit from "./Results/NonCashBenefit";

interface Savings {
  savingsAbsolute: number;
  savingsLeasingAbsolute: number;
  savingsLeasingPercent: number;
  savingsPercent: number;
}

export interface ComparisonColumn {
  taxBasis: number;
  contributions: Salary;
  salaryGross: number;
  salaryNet: number;
}

export interface ComparisonColumnWithBikes extends ComparisonColumn {
  leasingRate: number;
  leasingTotal: number;
  salaryGross: number;
  salaryNet: number;
  takeoverPrice: number;
  totalCost: number;
}

export interface Comparison {
  withoutBikes: ComparisonColumn;
  withBikes: ComparisonColumnWithBikes;
}

export interface Results {
  comparison?: Comparison;
  settings?: Settings;
  inputParams?: InputParams;
  savings?: Savings;
  bikePrices?: BikePrices;
  benefit?: EmployerBenefit;
}

interface ResultsProps {
  results?: Results;
  extended?: boolean;
  layout?: "rows" | "columns";
}

const Result = ({ results, extended, layout = "rows" }: ResultsProps) => {
  // const { comparison } = results || {};
  // const { withBikes } = comparison || {};

  // useEffect(() => {
  //   // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  //   // @ts-ignore
  //   window.calcResult = formatEur(withBikes?.leasingRate);
  // }, [results]);

  return layout === "rows" ? (
    <ResultRows extended={extended} results={results} />
  ) : (
    <ResultColumns extended={extended} results={results} />
  );
};

export default Result;

const ResultColumns = ({ results, extended }: ResultsProps) => {
  const { comparison, inputParams, savings, bikePrices, settings } =
    results || {};
  const { withBikes } = comparison || {};

  let insurancePackage = "";
  settings?.insurancePackages?.map(({ name, title }) => {
    if (name === inputParams?.insurancePackage) insurancePackage = title || "";
    return insurancePackage;
  });

  return (
    <div className="max-w-full">
      <div className="shadow-lg rounded-lg my-4 p-4 bg-white">
        {extended ? (
          <>
            <h2 className="text-center text-xl mb-2">
              {_t("yourPriceIndividual")}
            </h2>
            <p className="mt-2">
              ({_t("incl")} {insurancePackage}-{_t("package")}
              <Hint url={settings?.linkToInsurances}>
                <HintTable></HintTable>
              </Hint>
              )
            </p>
          </>
        ) : (
          <h2 className="text-center text-xl mb-2">{_t("yourPrice")}</h2>
        )}
        <div className="flex flex-wrap">
          <Col className="border-l-0">
            <div className="text-3xl text-[--color-primary]">
              {formatEur(withBikes?.leasingRate)}
            </div>
            <div className="text-md">
              ({_t("instead")} {formatEur(bikePrices?.leasingRate)})
            </div>
            <div className="text-xs py-2 text-[--color-text-light]">
              {_t("monthly")}
            </div>

            {bikePrices && (
              <Hint buttonText={_t("showDetails")}>
                <div className="pb-6">
                  <LeasingCosts bikePrices={bikePrices} />
                </div>
                <YourPrice bikePrices={bikePrices} withBikes={withBikes} />
              </Hint>
            )}
          </Col>
          <Col>
            <div className="text-3xl text-[--color-primary]">
              {formatPercent(savings?.savingsLeasingPercent)}
            </div>
            <div className="text-md">
              (-{formatEur(savings?.savingsLeasingAbsolute)})
            </div>
            <div className="text-xs py-2 text-[--color-text-light]">
              {_t("saving")} {_t("againstLeasing")}
            </div>
            {bikePrices && (
              <Hint buttonText={_t("showDetails")}>
                <div className="pb-6">
                  <YourPrice bikePrices={bikePrices} withBikes={withBikes} />
                </div>
                <ComparisonLeasing
                  bikePrices={bikePrices}
                  withBikes={withBikes}
                  results={results}
                />
              </Hint>
            )}
          </Col>
          <Col>
            <div className="text-3xl text-[--color-primary]">
              {formatPercent(savings?.savingsPercent)}
            </div>
            <div className="text-md">
              (-{formatEur(savings?.savingsAbsolute)})
            </div>
            <div className="text-xs py-2 text-[--color-text-light]">
              {_t("saving")} {_t("againstPurchase")}
            </div>
            {results?.comparison && (
              <Hint buttonText={_t("showDetails")}>
                <ComparisonPurchase results={results} />
              </Hint>
            )}
          </Col>
        </div>

        <div className="text-center pt-4">
          {results?.comparison && (
            <Hint buttonText={_t("showCalculation")}>
              <h3>{_t("comparisonWithToWithoutBike")}</h3>
              <SalaryCalculation results={results} />
            </Hint>
          )}
        </div>
      </div>
      <div className="text-lg text-[--color-text-light]">{_t("smallText")}</div>
      {/* <SalaryCalculation extended results={results} /> */}
    </div>
  );
};

