const ResultsTable = ({
  results,
  bikePrices,
}: {
  results?: Results;
  bikePrices?: BikePrices;
}) => {
  const [showDetails, setShowDetails] = useState(false);

  useEffect(() => {
    setShowDetails(results?.comparison ? true : false);
  }, [results?.comparison]);

  return (
    <>
      <div
        className="border-l border-l-4 border-l-[--color-primary] bg-[--color-result-detail-button] p-8 mt-4 uppercase text-xl font-display flex justify-between cursor-pointer"
        onClick={() => setShowDetails(!showDetails)}
      >
        <span>{_t("showExtended")}</span> <span>{showDetails ? "-" : "+"}</span>
      </div>
      <div className={`flex flex-col gap-4 ${showDetails ? "" : "hidden"}`}>
        <NonCashBenefit bikePrices={bikePrices} />
        <SalaryCalculation extended results={results} />
      </div>
    </>
  );
};
