// https://www.tk.de/firmenkunden/versicherung/beitraege-faq/beitragssaetze/tk-beitragssatz-2020-2073894
// https://www.tk.de/resource/blob/2113704/a12b6924abd42f5f64fde6a4bd80740d/beitragstabelle-2022-data.pdf

import { BMFCalculator } from "./calculate";

export interface Insurances {
  employee: {
    total: number;
    aVers: number;
    rVers: number;
    pVers: number;
    kVers: number;
  };
  employer: {
    total: number;
    aVers: number;
    rVers: number;
    pVers: number;
    kVers: number;
  };
}

const getInsurances = function ({
  healthInsuranceFactor,
  childrenCount = 0,
  taxBasis,
  b,
}: {
  healthInsuranceFactor?: number;
  taxBasis: number;
  childrenCount?: number;
  b: BMFCalculator;
}) {
  // Beitragssätze
  const aVersSatz = 0.026; // Arbeitslosenversicherung 2,60% 2024
  const aVersSatzAn = aVersSatz / 2; // hälfte wird von Arbeitnehmer getragen

  const pVersSatz = 0.04; // 4% Pflegeversicherung 2024 ab Juli
  const pVersSachsen = 0.005; // + 0,05% für Sachsen
  let pVersSatzAg = 0.017; // 4% / 2 Arbeitgeberanteil
  let pVersSatzAn = pVersSatz - pVersSatzAg; // 1,7% wird von Arbeitnehmer getragen
  let pVersKinderAbzug = 0;
  let childrenAfterFirst = 0;
  const kinderAbzugJeKind = 0.0025;
  const maxKinderAbzug = 5;

  if (childrenCount > 0) {
    pVersKinderAbzug = 0.006; // - 0,6% für erstes Kind
    if (childrenCount > 1) {
      childrenAfterFirst = childrenCount - 1; // + 0,25% für weitere Kinder
      pVersKinderAbzug +=
        kinderAbzugJeKind * Math.min(childrenAfterFirst, maxKinderAbzug);
    }
  }
  pVersKinderAbzug = Math.round(pVersKinderAbzug * 10000) / 10000;

  pVersSatzAn -= pVersKinderAbzug;
  if (b.PVS == 1) pVersSatzAn += pVersSachsen;
  if (b.PVS == 1) pVersSatzAg -= pVersSachsen;
  // var pVersSatzAn = b.PVSATZAN;
  // var pVersSatzAg = b.PVSATZAG;
  const kVersSatz = 0.146; // 14,60% in 2024
  const kVersSatzAn = (kVersSatz + (healthInsuranceFactor || 1) / 100) / 2; // häfte von Krankenversicherung
  // const kVersSatzAg = kVersSatzAn;
  const rVersSatz = 0.186; // 18,60 %
  const rVersSatzAn = rVersSatz / 2; // hälfte wird von Arbeitnehmer getragen;
  // Beitragsbemessungsgrenzen
  const kVersGrenze = 5175; // 5175€ in 2024
  const aVersGrenze = 7450; // 7450 € in 2024
  const aVersGrenzeOst = 7550; // 7550 € in 2024

  let pVers: number = 0,
    agPVers: number = 0,
    kVers: number = 0,
    rVers: number = 0,
    aVers: number = 0;

  if (b.PKV < 1) {
    // wenn gesetzlich krankenversichert
    // Bemessungsgrundlage Krankenversicherung und Pflegeversicherung
    const kpVersGrundlage = taxBasis > kVersGrenze ? kVersGrenze : taxBasis;

    // Pflegeversicherung Arbeitnehmer
    pVers = kpVersGrundlage * pVersSatzAn;
    // Pflegeversicherung Arbeitgeber
    agPVers = kpVersGrundlage * pVersSatzAg;

    // Krankenversicherung Arbeitnehmer
    kVers = kpVersGrundlage * kVersSatzAn;
  } else {
    // wenn privat krankenversichert
    kVers = (b.PKPV as number) / 100;
  }

  if (b.KRV == 2) {
    rVers = 0;
  } else {
    // Bemessungsgrundlage Arbeitslosenversicherung und Rentenversicherung
    let arVersGrundlage = taxBasis > aVersGrenze ? aVersGrenze : taxBasis;
    if (b.KRV == 1)
      arVersGrundlage = taxBasis > aVersGrenzeOst ? aVersGrenzeOst : taxBasis;

    // Rentenversicherung Arbeitnehmer
    rVers = arVersGrundlage * rVersSatzAn;

    // Arbeitslosenversicherung Arbeitnehmer
    aVers = arVersGrundlage * aVersSatzAn;
  }

  // Versicherungen gesamt (Arbeitslosenversicherung, Rentenversicherung, Krankenversicherung, Pflegeversicherung)
  // Arbeitnehmer
  const employee = {
    total: aVers + rVers + kVers + pVers,
    aVers,
    rVers,
    kVers,
    pVers,
  };
  // Arbeitgeber
  const employer = {
    total: aVers + rVers + agPVers + kVers,
    aVers,
    rVers,
    pVers: agPVers,
    kVers,
  };

  const insurances: Insurances = { employee, employer };
  return insurances;
};

export default getInsurances;
