import { createContext } from "react";
import { OptionsObject, mergeOptions } from "../../utils/mergeOptions";
import { Options } from "../../defaultOptions";
import Theme from "./Theme";

export const OptionContext = createContext({ options: {} as Options });

export default function OptionContextProvider({
  options,
  defaultOptions,
  children,
}: {
  options: object;
  defaultOptions: Options;
  children: React.ReactNode;
}) {
  const mergedOptions = mergeOptions(
    defaultOptions as OptionsObject,
    options as OptionsObject
  );
  return (
    <OptionContext.Provider value={{ options: mergedOptions }}>
      <Theme />
      {children}
    </OptionContext.Provider>
  );
}
