interface SectionProps {
  noBorder?: boolean;
  children: React.ReactNode;
  className?: string;
}

const Section = ({ noBorder, children, className }: SectionProps) => {
  return (
    <div
      className={`flex flex-col px-10 max-md:px-4 gap-4 ${
        noBorder ? "border-0 border-[--color-border] border-b-1" : ""
      } ${className || ""}`}
    >
      {children}
    </div>
  );
};

export default Section;
