import _t from "../../../utils/translations";
import { formatEur, formatPercent } from "../../../utils/numbers";
import { Results } from "../Result";
import { useOptions } from "../../../utils/useOptions";

interface ComparisonPurchaseProps {
  results?: Results;
}

const ComparisonPurchase = ({ results }: ComparisonPurchaseProps) => {
  const { comparison, inputParams, savings, bikePrices } = results || {};
  const { withBikes } = comparison || {};
  const { options } = useOptions();
  const { settings } = options;
  const { repairCost } = settings || {};
  return (
    <div>
      <h3 className="pr-8">{_t("comparisonPurchaseToLeasing")}</h3>
      {repairCost && (
        <p className="text-sm mb-2">
          {_t("insuranceCostInclusive1")} {formatEur(repairCost)}{" "}
          {_t("insuranceCostInclusive2")} ({_t("insuranceCostExplaination")})
        </p>
      )}
      <div>
        <table className="[&_td]:text-right">
          <thead>
            <tr className="">
              <th></th>
              <th>{_t("purchase")}</th>
              <th>{_t("leasing")}</th>
            </tr>
          </thead>
          <tbody>
            <tr className="border-top ">
              <th>{_t("leasingRateWithBike")}</th>
              <td></td>
              <td>
                {inputParams?.leasingPeriod} {_t("months")} ×{" "}
                {formatEur(withBikes?.leasingRate)} <br />
                =&nbsp;{formatEur(withBikes?.leasingTotal)}
              </td>
            </tr>
            <tr className="">
              <th>{_t("takeoverPrice")}</th>
              <td>{formatEur(bikePrices?.bikePrice)}</td>
              <td>{formatEur(withBikes?.takeoverPrice)}</td>
            </tr>
            <tr className="alternate">
              <th>{_t("totalCostInclInsurance")}</th>
              <td>=&nbsp;{formatEur(bikePrices?.bikesPlusRepair)}</td>
              <td>=&nbsp;{formatEur(withBikes?.totalCost)}</td>
            </tr>
            <tr className="alternate">
              <th>{_t("savingsAgainstPurchase")}</th>
              <td></td>
              <td colSpan={2}>
                {formatEur(bikePrices?.bikesPlusRepair)}&nbsp;
                <br />
                -&nbsp;{formatEur(withBikes?.totalCost)}
                <br />
                =&nbsp;{formatEur(savings?.savingsAbsolute)}
              </td>
            </tr>
            <tr className="alternate">
              <th>{_t("savingsPercent")}</th>
              <td colSpan={2}>
                1&nbsp;-&nbsp;(
                {formatEur(withBikes?.totalCost)}) &nbsp;/&nbsp;
                {formatEur(bikePrices?.bikesPlusRepair)})
                <br />
                =&nbsp;{formatPercent(savings?.savingsPercent)}
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default ComparisonPurchase;
