import { useState } from "react";
import _t from "../../../utils/translations";
import { formatEur } from "../../../utils/numbers";
import NonCashBenefit from "./NonCashBenefit";
import Button from "../../common/Button";
import Hint from "../../common/Hint";
import { Results } from "../Result";

interface SalaryCalculationProps {
  results?: Results;
  extended?: boolean;
}

const SalaryCalculation = ({ results, extended }: SalaryCalculationProps) => {
  const [showExtended, setShowExtended] = useState(extended);
  const [showDetailsTaxes, setShowDetailsTaxes] = useState(false);
  const [showDetailsInsurances, setShowDetailsInsurances] = useState(false);
  const { comparison, inputParams, settings, bikePrices, benefit } =
    results || {};
  const { withBikes, withoutBikes } = comparison || {};

  return (
    <>
      <div>
        <table className="[&_td]:text-right">
          <thead>
            <tr className="">
              <th>{_t("comparisonWithToWithoutBike")}</th>
              <th className="text-right">{_t("withoutBikes")}</th>
              <th className="text-right">{_t("withBikes")}</th>
            </tr>
          </thead>
          <tbody>
            <tr className="alternate">
              <th>
                {results?.inputParams?.workTypeOfficial
                  ? _t("salaryGrossOfficial")
                  : _t("salaryGross")}
              </th>
              <td>{formatEur(inputParams?.salaryGross)}</td>
              <td>{formatEur(inputParams?.salaryGross)}</td>
            </tr>
            <tr>
              <th>
                {_t("leasingRate")} {_t("leasingRateAddition")}
              </th>
              <td></td>
              <td>-&nbsp;{formatEur(bikePrices?.leasingRate)}</td>
            </tr>

            <tr
              className={`${
                benefit?.other && benefit?.other > 0 ? "" : "hidden"
              }`}
            >
              <th>{_t("benefitOther")}</th>
              <td>+&nbsp;{formatEur(benefit?.other)}</td>
              <td>+&nbsp;{formatEur(benefit?.other)}</td>
            </tr>
            <tr
              className={`${
                benefit?.bikes && benefit?.bikes > 0 ? "" : "hidden"
              }`}
            >
              <th>{_t("benefitTable")}</th>
              <td></td>
              <td>+&nbsp;{formatEur(benefit?.bikes)}</td>
            </tr>
            <tr
              className={`${
                bikePrices?.bikePriceVatToSubtract &&
                bikePrices?.bikePriceVatToSubtract > 0
                  ? ""
                  : "hidden"
              }`}
            >
              <th>{_t("bikePriceVatToSubtract")}</th>
              <td></td>
              <td>-&nbsp;{formatEur(bikePrices?.bikePriceVatToSubtract)}</td>
            </tr>
            <tr className="alternate">
              <th>{_t("salaryWithBikes")}</th>
              <td></td>
              <td>=&nbsp;{formatEur(withBikes?.salaryGross)}</td>
            </tr>
            <tr>
              <th>
                {_t("plusNonCashBenefit")}{" "}
                <Hint>
                  <NonCashBenefit
                    bikePrices={bikePrices}
                    useFullBikeUvpPrice={settings?.useFullBikeUvpPrice || false}
                  />
                </Hint>
              </th>
              <td></td>
              <td>+&nbsp;{formatEur(bikePrices?.bikePricePercent)}</td>
            </tr>
            <tr className="alternate">
              <th>{_t("taxBasis")}</th>
              <td>=&nbsp;{formatEur(withoutBikes?.taxBasis)}</td>
              <td>=&nbsp;{formatEur(withBikes?.taxBasis)}</td>
            </tr>

            <tr>
              <th>
                <div className="flex flex-nowrap gap-3">
                  <Button
                    size="mini"
                    noPadding
                    className="rounded-none aspect-square w-5 leading-none text-xs"
                    variant="primary"
                    circle
                    onClick={() => setShowDetailsTaxes(!showDetailsTaxes)}
                  >
                    {showDetailsTaxes ? "-" : "+"}
                  </Button>{" "}
                  {_t("taxes")}
                </div>
              </th>
              <td>
                -&nbsp;{formatEur(withoutBikes?.contributions?.taxes?.total)}
              </td>
              <td>
                -&nbsp;{formatEur(withBikes?.contributions?.taxes?.total)}
              </td>
            </tr>

            {showDetailsTaxes && (
              <>
                <tr>
                  <th className="indent">{_t("churchTax")}</th>
                  <td>
                    {formatEur(withoutBikes?.contributions?.taxes?.church)}
                  </td>
                  <td>{formatEur(withBikes?.contributions?.taxes?.church)}</td>
                </tr>
                <tr>
                  <th className="indent">{_t("solidarityTax")}</th>
                  <td>{formatEur(withoutBikes?.contributions?.taxes?.soli)}</td>
                  <td>{formatEur(withBikes?.contributions?.taxes?.soli)}</td>
                </tr>
                <tr>
                  <th className="indent">{_t("incomeTax")}</th>
                  <td>
                    {formatEur(withoutBikes?.contributions?.taxes?.income)}
                  </td>
                  <td>{formatEur(withBikes?.contributions?.taxes?.income)}</td>
                </tr>
              </>
            )}
            <tr>
              <th>
                <div className="flex flex-nowrap gap-3">
                  <Button
                    size="mini"
                    noPadding
                    className="rounded-none aspect-square w-5 leading-none text-xs"
                    variant="primary"
                    circle
                    onClick={() =>
                      setShowDetailsInsurances(!showDetailsInsurances)
                    }
                  >
                    {showDetailsInsurances ? "-" : "+"}
                  </Button>{" "}
                  {_t("socialInsurances")}
                </div>
              </th>
              <td>
                -&nbsp;
                {formatEur(
                  withoutBikes?.contributions?.insurances?.employee?.total
                )}
              </td>
              <td>
                -&nbsp;
                {formatEur(
                  withBikes?.contributions?.insurances?.employee?.total
                )}
              </td>
            </tr>
            {showDetailsInsurances && (
              <>
                <tr>
                  <th className="indent">{_t("unemploymentInsurance")}</th>
                  <td>
                    {formatEur(
                      withoutBikes?.contributions?.insurances?.employee?.aVers
                    )}
                  </td>
                  <td>
                    {formatEur(
                      withBikes?.contributions?.insurances?.employee?.aVers
                    )}
                  </td>
                </tr>
                <tr>
                  <th className="indent">{_t("pensionInsurance")}</th>
                  <td>
                    {formatEur(
                      withoutBikes?.contributions?.insurances?.employee?.rVers
                    )}
                  </td>
                  <td>
                    {formatEur(
                      withBikes?.contributions?.insurances?.employee?.rVers
                    )}
                  </td>
                </tr>
                <tr>
                  <th className="indent">{_t("careInsurance")}</th>
                  <td>
                    {formatEur(
                      withoutBikes?.contributions?.insurances?.employee?.pVers
                    )}
                  </td>
                  <td>
                    {formatEur(
                      withBikes?.contributions?.insurances?.employee?.pVers
                    )}
                  </td>
                </tr>
                <tr>
                  <th className="indent">{_t("healthInsurance")}</th>
                  <td>
                    {formatEur(
                      withoutBikes?.contributions?.insurances?.employee?.kVers
                    )}
                  </td>
                  <td>
                    {formatEur(
                      withBikes?.contributions?.insurances?.employee?.kVers
                    )}
                  </td>
                </tr>
              </>
            )}
            <tr className="alternate">
              <th>{_t("salaryNet")}</th>
              <td>
                =&nbsp;{formatEur(withoutBikes?.contributions?.salaryNet)}
              </td>
              <td>=&nbsp;{formatEur(withBikes?.contributions?.salaryNet)}</td>
            </tr>
            <tr>
              <th>{_t("minusNonCashBenefit")}</th>
              <td></td>
              <td>-&nbsp;{formatEur(bikePrices?.bikePricePercent)}</td>
            </tr>

            <tr className="alternate font-display">
              <th>{_t("payout")}</th>
              <td>
                =&nbsp;{formatEur(withoutBikes?.contributions?.salaryNet)}
              </td>
              <td>=&nbsp;{formatEur(withBikes?.salaryNet)}</td>
            </tr>
            <tr className="alternate font-display text-lg">
              <th>{_t("realNet")}</th>
              <td></td>
              <td>
                {formatEur(withoutBikes?.contributions?.salaryNet)} <br />-{" "}
                {formatEur(withBikes?.salaryNet)}
                <br />
                =&nbsp;{formatEur(withBikes?.leasingRate)}
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <div className="text-center py-4 hidden">
        <Button onClick={() => setShowExtended(!showExtended)} type="link">
          {showExtended ? (
            <>{_t("showOnlySummary")} -</>
          ) : (
            <>{_t("showExtended")} +</>
          )}
        </Button>
      </div>
    </>
  );
};

export default SalaryCalculation;
