import { InputParams, Settings } from "../../../defaultOptions";

function getInsurancePackage({
  inputParams,
  settings,
}: {
  inputParams: InputParams;
  settings: Settings;
}) {
  return (
    settings?.insurancePackages?.filter(({ name }) => {
      return name === inputParams?.insurancePackage;
    })[0] || {}
  );
}
export default getInsurancePackage;
