import getPercentBikePrice from "./getPercentBikePrice";
import getLeasingPrice from "./getLeasingPrice";
import getLeasingInsurance, {
  BikeLeasingInsurancesPerBike,
} from "./getLeasingInsurance";
import { InputParams, Settings } from "../../../defaultOptions";

export interface BikePrices {
  bikePrice: number;
  bikesPlusRepair: number;

  bikePriceVatToSubtract: number;
  bikePricePercent: number;
  bikePriceQuarterUvp: number;
  bikePricePercentFull: number;
  bikesPriceFractionUvp: number;
  leasingRate: number;
  leasingRateWithoutInsurance: number;
  leasingRateWithoutInsuranceNet: number;
  leasingRateFactor: number;
  leasingRateNormalTotal: number;
  leasingRateInsuranceTotal: number;
  leasingRateInsuranceTotalNet: number;
  leasingRateInsurancePerMonth: number;
  leasingRateInsurancePerMonthNet: number;
  bikePriceWithLeasingInsurance: number;
  leasingRateInsurancesPerBike: BikeLeasingInsurancesPerBike[];
}

function getBikePrices({
  inputParams,
  settings,
}: {
  inputParams: InputParams;
  settings: Settings;
}) {
  if (!inputParams?.totalPriceUVP) return null;

  const bikePrice = inputParams?.totalPrice || 0;
  const leasingRateInsurance = getLeasingInsurance({
    inputParams,
    settings,
  });

  if (!leasingRateInsurance) return null;

  const {
    leasingRateInsurancePerMonth,
    leasingRateInsurancePerMonthNet,
    leasingRateInsuranceTotal,
    leasingRateInsuranceTotalNet,
    bikePriceWithLeasingInsurance,
    leasingRateInsurancesPerBike,
  } = leasingRateInsurance;

  const {
    leasingRate,
    leasingRateFactor,
    leasingRateWithoutInsurance,
    leasingRateWithoutInsuranceNet,
  } = getLeasingPrice({
    inputParams,
    settings,
    bikePrice,
    leasingRateInsurancePerMonth,
    leasingRateInsurancePerMonthNet,
    bikePriceWithLeasingInsurance,
  });

  const percentBikePrices = getPercentBikePrice({
    inputParams,
    settings,
  });

  if (!percentBikePrices) return null;

  const {
    bikePricePercent,
    bikePriceQuarterUvp,
    bikePricePercentFull,
    bikesPriceFractionUvp,
  } = percentBikePrices;

  const repairCostTotal =
    (settings?.repairCost || 400) * (inputParams?.bikeCount || 1);
  const bikesPlusRepair = (inputParams?.totalPrice || 0) + repairCostTotal;

  const bikePriceVatToSubtract = settings?.subtractTaxAfterEmployerBenefit
    ? Math.round(bikesPriceFractionUvp * 19) / 119
    : 0;

  const leasingRateNormalTotal =
    leasingRate * (inputParams?.leasingPeriod || 36);

  const bikePrices: BikePrices = {
    bikePrice,
    bikePriceVatToSubtract,
    bikePricePercent,
    bikePriceQuarterUvp,
    bikePricePercentFull,
    bikesPriceFractionUvp,
    leasingRate,
    leasingRateWithoutInsurance,
    leasingRateWithoutInsuranceNet,
    leasingRateFactor,
    bikesPlusRepair,
    leasingRateNormalTotal,
    leasingRateInsurancePerMonth,
    leasingRateInsurancePerMonthNet,
    bikePriceWithLeasingInsurance,
    leasingRateInsuranceTotal,
    leasingRateInsuranceTotalNet,
    leasingRateInsurancesPerBike,
  };

  return bikePrices;
}

export default getBikePrices;
