import { useState } from "react";

interface ToggleProps {
  onChange?: (value: boolean) => void;
  initialValue?: boolean | string;
}

const Toggle = ({ onChange = () => {}, initialValue }: ToggleProps) => {
  const [value, setValue] = useState(
    typeof initialValue === "boolean"
      ? initialValue
      : initialValue === "AG"
      ? true
      : false
  );

  const handleClick = () => {
    const newValue = !value;
    setValue(newValue);
    onChange(newValue);
  };
  return (
    <div>
      <div
        onClick={handleClick}
        className={`relative text-sm h-10 select-none justify-between items-center inline-flex w-28 border-0 p-2 cursor-pointertext-white transition-all text-white ${
          value === true ? `bg-[--color-yes]` : `bg-[--color-no]`
        }`}
      >
        <span
          className={`absolute h-6 w-12 shrink-0 bg-white z-10 ${
            value === true ? "left-[calc(100%-58px)]" : "left-[5px]"
          } transition-all`}
        ></span>
        <span
          className={`px-2 text-center flex-grow transition-opacity ${
            value === true ? "opacity-100" : "opacity-0"
          }`}
        >
          Ja
        </span>
        <span
          className={`px-2 text-center flex-grow transition-opacity ${
            value === true ? "opacity-0" : "opacity-100"
          }`}
        >
          Nein
        </span>
      </div>
    </div>
  );
};

export default Toggle;
