import { InputParams, Settings } from "../../../defaultOptions";

export interface PercentBikePrice {
  bikePricePercent: number;
  bikePriceQuarterUvp: number;
  bikesPriceFractionUvp: number;
  bikePricePercentFull: number;
}

function getPercentBikePrice({
  inputParams,
  settings,
}: {
  inputParams: InputParams;
  settings: Settings;
}) {
  if (!inputParams?.totalPriceUVP) return null;
  const bikePriceQuarterUvp = inputParams?.totalPriceUVP / 4;

  // 1% vom Viertel-Fahrradneupreis auf volle hundert abgerundet
  const bikePricePercent = Math.floor(bikePriceQuarterUvp / 100);
  // 1% vom Fahrradneupreis auf volle hundert abgerundet
  const bikePricePercentFull = Math.floor(inputParams?.totalPriceUVP / 100);

  const bikesPriceFractionUvp = settings?.useFullBikeUvpPrice
    ? bikePricePercentFull
    : bikePricePercent;

  const percentBikePrice: PercentBikePrice = {
    bikesPriceFractionUvp,
    bikePricePercent,
    bikePriceQuarterUvp,
    bikePricePercentFull,
  };

  return percentBikePrice;
}

export default getPercentBikePrice;
