import getTaxes, { Taxes } from "./getTaxes";
import getInsurances, { Insurances } from "./2024getInsurances";
import { InputParams } from "../../../defaultOptions";

export interface Salary {
  taxes: Taxes;
  insurances: Insurances;
  contributionsTotal: number;
  salaryNet: number;
}

function getSalaryNetto({
  taxBasis,
  inputParams,
}: {
  taxBasis: number;
  inputParams: InputParams;
}) {
  const taxes = getTaxes({
    inputParams,
    taxBasis,
  });

  const insurances = getInsurances({
    healthInsuranceFactor: inputParams?.healthInsuranceFactor,
    childrenCount: inputParams?.hasChildren ? inputParams?.childrenCount : 0,
    taxBasis,
    b: taxes.b,
  });

  const contributionsTotal = taxes.total + insurances.employee.total;
  const salaryNet = taxBasis - contributionsTotal;

  const output: Salary = {
    salaryNet,
    taxes,
    insurances,
    contributionsTotal,
  };

  return output;
}

export default getSalaryNetto;
