import { useEffect, useState } from "react";

interface InputProps {
  initialValue: number;
  onChange: (newValue: number) => void;
  prefix?: string;
  type: "number" | "text";
  calculatedValue?: number;
  step?: number;
  min?: number;
  max?: number;
  disabled?: boolean;
  placeholder?: string;
  variant?: string;
  required?: boolean;
}

const Input = ({
  initialValue = 0,
  calculatedValue,
  onChange = () => {},
  prefix = "",
  type,
  step = 1,
  min = 0,
  max = Infinity,
  disabled,
  placeholder = "",
  variant = "normal",
  required = false,
}: InputProps) => {
  const [value, setValue] = useState(initialValue);
  const [hasChanged, setHasChanged] = useState(false);

  useEffect(() => {
    if (value > max) setValue(max);
  }, [max]);

  useEffect(() => {
    if (calculatedValue && !hasChanged) setValue(calculatedValue);
  }, [calculatedValue]);

  const handleChange = (e: { target: { value: string } }) => {
    const newValue = type === "number" ? parseFloat(e?.target?.value) : 0;
    setHasChanged(true);
    setValue(newValue);
  };

  const handleBlur = (e: { target: { value: string } }) => {
    let newValue = type === "number" ? parseFloat(e.target.value) : 0;

    if (type === "number") {
      if (max && newValue > max) newValue = max;
      if (min && newValue < min) newValue = min;
      if (isNaN(newValue)) newValue = min;
    }
    setValue(newValue);
    onChange(newValue);
  };
  return (
    <div
      className={`flex w-full items-center text-right border border-[--color-border] font-normal ${
        variant !== "big" ? "text-sm" : "text-lg"
      } ${required && !value ? "bg-[--color-input-required]" : ""} ${
        disabled ? "bg-[--color-input-disabled]" : "bg-[--color-input]"
      }`}
    >
      <span className={`px-4 leading-none`}>{prefix}</span>
      <input
        placeholder={placeholder}
        type={type}
        value={value}
        min={type === "number" ? min : 0}
        max={type === "number" ? max : Infinity}
        step={step}
        onChange={handleChange}
        onBlur={handleBlur}
        disabled={disabled}
        className={`border-0 px-4 ${
          variant === "big" ? "py-3.5" : "py-2"
        } bg-transparent text-right w-full`}
      />
    </div>
  );
};

export default Input;
