import _t from "../../utils/translations";

interface ButtonProps {
  size?: "big" | "medium" | "small" | "mini";
  type?: "link" | "button";
  circle?: boolean;
  loading?: boolean;
  children?: React.ReactNode;
  style?: React.CSSProperties;
  onClick?: () => void;
  className?: string;
  noPadding?: boolean;
  variant?: "primary" | "secondary" | "tertiary";
}

const Button = ({
  size = "medium",
  type = "button",
  circle = false,
  noPadding,
  loading,
  children,
  variant = "secondary",
  className,
  onClick,
}: ButtonProps) => {
  const padding = noPadding
    ? "p-0"
    : type === "link"
    ? "px-0"
    : size === "big"
    ? "py-2 px-4"
    : size === "medium"
    ? "py-1.5 px-2 text-sm"
    : size === "small"
    ? "py-1.5 px-2 text-xs"
    : size === "mini"
    ? "px-1.5 py-1.5 text-xs"
    : "";

  const fontSize =
    size === "big"
      ? "text-lg"
      : size === "medium"
      ? "text-sm"
      : size === "small"
      ? "text-xs"
      : size === "mini"
      ? "text-xs"
      : "";

  const circleStyles = circle
    ? "rounded-full shrink-0 aspect-square leading-[0px] inline-flex items-center justify-center"
    : "";

  const colors =
    type === "link"
      ? "bg-transparent border-0 text-[--color-primary] font-normal"
      : variant === "primary"
      ? "bg-[--color-button] text-[--color-button-text]"
      : variant === "secondary"
      ? "bg-[--color-button-light] text-[--color-button-text]"
      : "";

  return (
    <button
      onClick={onClick}
      className={`${
        loading
          ? "opacity-50 pointer-events-none"
          : "opacity-100 pointer-events-auto"
      } border-1 rounded-full font-display cursor-pointer self-center ${colors} ${fontSize} ${padding} ${circleStyles} ${
        className || ""
      } `}
    >
      {!loading && children}
      <span className={`${loading ? "inline" : "hidden"}`}>
        {_t("loading")}
      </span>
    </button>
  );
};

export default Button;
