import _t from "../../utils/translations";
import { useOptions } from "../../utils/useOptions";
import { InsurancePackages } from "../../defaultOptions";

const HintTable = () => {
  const {
    options: { settings },
  } = useOptions();
  const { insurancePackages } = settings || {};

  const allFeatures: string[] = [];
  insurancePackages?.map(({ features }) => {
    if (features) {
      for (const [key] of Object.entries(features)) {
        allFeatures.push(key);
      }
    }
    return allFeatures;
  });
  const uniqueFeatures = [...new Set(allFeatures)];
  return (
    <div className="flex flex-col overflow-auto h-full w-full">
      <h2 className="text-left">{_t("compareTableHeadline")}</h2>
      <table className="w-full border-collapse border-spacing-0">
        <thead>
          <tr>
            <th className="text-left max-w-24">
              {_t("compareTableRepairOrReplacement")}
            </th>
            {insurancePackages?.map(({ name, title }: InsurancePackages) => (
              <th className="text-left max-w-24" key={name}>
                {title}
              </th>
            ))}
          </tr>
        </thead>
        <tbody>
          {uniqueFeatures.map((text) => (
            <tr key={text}>
              <th className="max-w-[200px]">{_t(`compareTable${text}`)}</th>
              {insurancePackages?.map(
                ({ name, features }: InsurancePackages) => {
                  const key = features?.[text];
                  return features instanceof Object ? (
                    name && (
                      <td key={name} className="max-w-16 text-center">
                        {key === true ? (
                          <Yes />
                        ) : key === false ? (
                          <No />
                        ) : typeof key === "string" ? (
                          _t(key)
                        ) : (
                          "N/A"
                        )}
                      </td>
                    )
                  ) : (
                    <td className="max-w-16 text-[#eee]">N/A</td>
                  );
                }
              )}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

const Yes = () => {
  const { options } = useOptions();
  return (
    <span
      className={`${
        options?.colors?.yes ? "bg-[--color-yes]" : "bg-[#6edc6e]"
      } w-6 h-6 inline-flex items-center justify-center rounded-full text-white`}
    >
      <svg
        width="16"
        height="11"
        viewBox="0 0 15 11"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="M5.33336 8.64325L12.9934 0.982422L14.1725 2.16076L5.33336 10.9999L0.0300293 5.69659L1.20836 4.51826L5.33336 8.64325Z"
          fill="currentColor"
          stroke="currentColor"
          strokeWidth="1"
        />
      </svg>
    </span>
  );
};
const No = () => (
  <span className="bg-[#ddd] font-display w-6 h-6 inline-flex items-center justify-center rounded-full text-white">
    -
  </span>
);

export default HintTable;
