import ReactDOM from "react-dom/client";
import { Options } from "./defaultOptions";
import EurLeasingCalcDe from "./components/EurLeasingCalcDe";
// import EurLeasingCalcAt from "./components/EurLeasingCalcAt";

export const normalizeAttribute = (attribute: string) => {
  return attribute.replace(/-([a-z])/g, (_, letter) => letter.toUpperCase());
};

export interface WebComponentProps {
  options: Options | string;
  styleSheet: string;
  country: string;
}

class EurLeasingCalcWebComponent extends HTMLElement {
  constructor() {
    super();
    this.attachShadow({ mode: "open" });
  }

  connectedCallback() {
    const props = this.getPropsFromAttributes<WebComponentProps>();
    const { options, styleSheet, country = "de" } = props;
    const root = ReactDOM.createRoot(this.shadowRoot as ShadowRoot);
    let parsedOptions;
    try {
      parsedOptions = JSON.parse(options as string);
    } catch (error) {
      parsedOptions = options;
    }
    root.render(
      country === "de" ? (
        <EurLeasingCalcDe options={parsedOptions} />
      ) : // : country === "at" ? (
      //   <EurLeasingCalcAt options={parsedOptions} />
      // )
      null
    );

    const styleSheetElm = document.createElement("link");
    styleSheetElm.rel = "stylesheet";
    styleSheetElm.href = styleSheet;
    this?.shadowRoot?.appendChild(styleSheetElm);
  }

  private getPropsFromAttributes<T>(): T {
    const props: Record<string, string> = {};

    for (let index = 0; index < this.attributes.length; index++) {
      const attribute = this.attributes[index];
      props[normalizeAttribute(attribute.name)] = attribute.value;
    }

    return props as T;
  }
}

export default EurLeasingCalcWebComponent;
