import { useOptions } from "./useOptions";

export default function _t(text: string) {
  const { options } = useOptions();
  const langKey = options?.settings?.language;
  if (!langKey) return text;
  const translation = options?.texts?.[langKey]?.[text];
  return translation ? (
    <span dangerouslySetInnerHTML={{ __html: translation }}></span>
  ) : (
    text
  );
}
