import { InputParams, Settings } from "../../../defaultOptions";
import { BikePrices } from "./getBikePrices";

interface EmployerBenefitParams {
  inputParams: InputParams;
  bikePrices?: BikePrices;
  settings: Settings;
}

export interface EmployerBenefit {
  bikes: number;
  other: number;
}

export default function getEmployerBenefit({
  settings,
  inputParams,
  bikePrices,
}: EmployerBenefitParams) {
  const benefitType = settings?.benefitType;
  let benefitBikes = 0;
  const employersshareBikeCount = Math.min(
    settings?.benefitBikeCount || 1,
    inputParams?.bikeCount || 1
  );

  if (benefitType === 0) {
    benefitBikes = 0;
  } else if (benefitType === 1) {
    benefitBikes =
      employersshareBikeCount *
      getPackageBenefit({
        settings,
        insurancePackage: inputParams?.insurancePackage || "",
      });
  } else if (benefitType === 2) {
    benefitBikes = inputParams?.benefit
      ? inputParams?.benefit * employersshareBikeCount
      : 0;
  } else if (benefitType === 3) {
    const percentEployerShare = inputParams?.benefit
      ? inputParams?.benefit / 100
      : 0;
    benefitBikes = bikePrices
      ? Math.round(bikePrices.leasingRate * percentEployerShare * 100) / 100
      : 0;
  } else if (benefitType === 4) {
    const benefitPercent = inputParams.benefit ? inputParams.benefit / 100 : 0;
    const leasingInsurancePerMonth = bikePrices
      ? bikePrices?.leasingRateInsurancePerMonthNet
      : 0;
    benefitBikes =
      Math.round(leasingInsurancePerMonth * benefitPercent * 100) / 100;
  } else if (benefitType === 5) {
    const benefitPercent = inputParams.benefit ? inputParams.benefit / 100 : 0;
    benefitBikes = inputParams?.totalPriceUVP
      ? Math.round(inputParams?.totalPriceUVP * benefitPercent * 100) / 100
      : 0;
  } else if (benefitType === 6) {
    benefitBikes = inputParams.benefit
      ? inputParams.benefit * employersshareBikeCount
      : 0;
  } else if (benefitType === 7) {
    benefitBikes = inputParams?.benefit
      ? inputParams?.benefit * employersshareBikeCount
      : 0;
  } else if (benefitType === 8) {
    const benefitPercent = inputParams.benefit ? inputParams.benefit / 100 : 0;
    const benefitPercent2 = inputParams.benefit2
      ? inputParams.benefit2 / 100
      : 0;
    const leasingInsurancePerMonth = bikePrices
      ? bikePrices?.leasingRateInsurancePerMonthNet
      : 0;

    const rateWithoutInsurance =
      bikePrices?.leasingRateWithoutInsuranceNet || 0;

    benefitBikes = bikePrices
      ? Math.round(rateWithoutInsurance * benefitPercent2 * 100) / 100 +
        Math.round(leasingInsurancePerMonth * benefitPercent * 100) / 100
      : 0;
  }

  const benefitAdditional = inputParams?.benefitAdditional?.reduce(
    (acc, curr) => acc + curr.value,
    0
  );

  let other = 0;
  other += benefitAdditional || 0;
  other += inputParams?.benefitAds || 0;
  other += inputParams?.benefitPhone || 0;
  other += inputParams?.benefitInternet || 0;

  const benefit: EmployerBenefit = {
    bikes: benefitBikes,
    other,
  };

  return benefit;
}

export function getPackageBenefit({
  settings,
  insurancePackage,
}: {
  settings: Settings;
  insurancePackage: string;
}) {
  return settings?.benefitPerInsurancePackage?.[insurancePackage] || 0;
}
