import { useOptions } from "../../utils/useOptions";

const Theme = () => {
  const {
    options: { colors },
  } = useOptions();

  if (!colors) return null;

  return (
    <style>
      {`
        #eur-leasingcalc {
            --color-text: ${colors.text};
            --color-text-required: ${colors.textRequired};
            --color-input-required: ${colors.inputRequired};
            --color-form-bg: ${colors.formBg};
            --color-yes: ${colors.yes};
            --color-no: ${colors.no};
            --color-primary: ${colors.primary};
            --color-button: ${colors.button};
            --color-button-text: ${colors.buttonText};
            --color-button-light: ${colors.buttonLight};
            --color-text-light: ${colors.textLight};
            --color-border: ${colors.border};
            --color-input: ${colors.input};
            --color-input-disabled: ${colors.inputDisabled};
            --color-result-row-alternate: ${colors.resultRowAlternate};
            --color-result-row-highlight: ${colors.resultRowHighlight};
            --color-result-row-head: ${colors.resultRowHead};
            --color-result-row-foot: ${colors.resultRowFoot};
            --color-result-detail-button: ${colors.resultDetailButton};
            --color-head-row-bg: ${colors.headRowBg};
            --color-head-box-bg: ${colors.headBoxBg};
            
        }
    `}
    </style>
  );
};

export default Theme;
