import getLeasingFactor from "./getLeasingFactor";
import { netto } from "../../../utils/numbers";
import { InputParams, Settings } from "../../../defaultOptions";

interface LeasingPriceParams {
  inputParams: InputParams;
  settings: Settings;
  leasingRateInsurancePerMonth: number;
  leasingRateInsurancePerMonthNet: number;
  bikePriceWithLeasingInsurance: number;
  bikePrice: number;
}

interface LeasingPrice {
  leasingRate: number;
  leasingRateFactor: number;
  leasingRateWithoutInsurance: number;
  leasingRateWithoutInsuranceNet: number;
}

function getLeasingPrice({
  inputParams,
  settings,
  leasingRateInsurancePerMonth,
  leasingRateInsurancePerMonthNet,
  bikePrice,
  bikePriceWithLeasingInsurance,
}: LeasingPriceParams) {
  const { leasingRateFactor } = getLeasingFactor({
    settings,
    inputParams,
    bikePrice,
    bikePriceWithLeasingInsurance,
  });

  const leasingRateWithoutInsurance = bikePrice * (leasingRateFactor / 100);

  const totalPricePerMonth =
    leasingRateWithoutInsurance + leasingRateInsurancePerMonth;

  const leasingRateWithoutInsuranceNet = netto(leasingRateWithoutInsurance);

  const totalPricePerMonthNet =
    leasingRateInsurancePerMonthNet + leasingRateWithoutInsuranceNet;

  const leasingRate = inputParams.employerTaxDeduction
    ? totalPricePerMonthNet
    : totalPricePerMonth;

  const leasingPrice: LeasingPrice = {
    leasingRate,
    leasingRateWithoutInsurance,
    leasingRateWithoutInsuranceNet,
    leasingRateFactor,
  };

  return leasingPrice;
}

export default getLeasingPrice;
