import _t from "../../../utils/translations";
import { formatEur, formatPercent } from "../../../utils/numbers";
import { ComparisonColumnWithBikes, Results } from "../Result";
import { BikePrices } from "../calc/getBikePrices";

interface ComparisonLeasingProps {
  bikePrices?: BikePrices;
  withBikes?: ComparisonColumnWithBikes;
  results?: Results;
}

const ComparisonLeasing = ({
  bikePrices,
  withBikes,
  results,
}: ComparisonLeasingProps) => {
  const { leasingPeriod } = results?.inputParams || {};
  return (
    <div>
      <h3 className="pr-8">{_t("comparisonLeasingCostTotal")}</h3>
      <div>
        <table className="[&_td]:text-right">
          <tbody>
            <tr className="hightlight">
              <th>{_t("comparisonLeasingCostTotalNormal")}</th>
              <td>
                {formatEur(bikePrices?.leasingRate)}&nbsp;×&nbsp;
                {leasingPeriod}&nbsp;{_t("months")}
                <br />
                =&nbsp;
                {formatEur(bikePrices?.leasingRateNormalTotal)}
              </td>
            </tr>
            <tr className="hightlight">
              <th>{_t("comparsionLeasingCostViaSalary")}</th>
              <td>
                {formatEur(withBikes?.leasingRate)}&nbsp;×&nbsp;
                {leasingPeriod}&nbsp;{_t("months")}
                <br />
                =&nbsp;{formatEur(withBikes?.leasingTotal)}
              </td>
            </tr>
            <tr className="hightlight">
              <th>{_t("savingsLeasingAbsolute")}</th>
              <td>
                {formatEur(bikePrices?.leasingRateNormalTotal)}&nbsp;-&nbsp;
                {formatEur(withBikes?.leasingTotal)}
                <br />
                =&nbsp;{formatEur(results?.savings?.savingsLeasingAbsolute)}
              </td>
            </tr>
            <tr className="hightlight">
              <th>{_t("savingsLeasingPercent")}</th>
              <td>
                1&nbsp;-&nbsp;({formatEur(withBikes?.leasingTotal)}
                &nbsp;/&nbsp;
                {formatEur(bikePrices?.leasingRateNormalTotal)}
                )<br />
                =&nbsp;
                {formatPercent(results?.savings?.savingsLeasingPercent)}
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default ComparisonLeasing;
