const VAT = 1.19;
/*______________NUMBER FUNCTIONS___________________*/

export const netto = (price: number) => {
  return price / VAT;
};

export const brutto = (price: number) => {
  return price * VAT;
};

export function formatEur(value: number | undefined) {
  if (value === undefined) return "-- €";
  return value.toLocaleString("de-DE", {
    style: "currency",
    currency: "EUR",
  });
}

export function formatPercent(value?: number) {
  if (value === undefined) return "-- %";
  return value.toFixed(0) + "%";
}
