import { memo, useRef } from "react";
import _t from "../../utils/translations";
import Button from "./Button";
import BikeInputRow, { Row } from "./BikeInputRow";
import { useOptions } from "../../utils/useOptions";
import { InputParams } from "../../defaultOptions";

interface BikeInputProps {
  initialValueUvp: number;
  initialValue: number;
  onChange?: (params: {
    totalPrice: number;
    totalPriceUVP: number;
    bikeCount: number;
    bikeRows: Row[];
  }) => void;
  maxBikes: number;
  extended: boolean;
  sendParams: InputParams;
  maxBikePrice?: number;
}

const BikeInput = memo(
  ({
    initialValueUvp = 0,
    initialValue = 0,
    onChange = () => {},
    maxBikes,
    extended,
    maxBikePrice = Infinity,
  }: BikeInputProps) => {
    const { options } = useOptions();
    const { settings } = options;
    const uid = useRef(0);
    const initialRow = {
      price: initialValue,
      uvp: initialValueUvp,
      uid: uid.current,
    };
    const bikeRows = useRef([initialRow]);
    const prices = useRef({ total: initialValue, totalUvp: initialValueUvp });

    const handleBikeRowChange = () => {
      if (bikeRows.current.length < 1) return;
      let newTotalPrice: number = 0;
      let newTotalPriceUVP: number = 0;

      bikeRows.current.map(({ price, uvp }) => {
        newTotalPrice += price;
        newTotalPriceUVP += uvp;
        return null;
      });

      prices.current.total = newTotalPrice;
      prices.current.totalUvp = newTotalPriceUVP;

      const bikeCount = bikeRows.current.length;

      onChange({
        totalPrice: newTotalPrice,
        totalPriceUVP: newTotalPriceUVP,
        bikeCount,
        bikeRows: bikeRows.current,
      });
    };

    if (!settings) return null;
    const { minBikePrice } = settings;
    const { isProduct } = settings;

    const addBike = () => {
      const newBikeRows = [...bikeRows.current];

      if (newBikeRows.length >= maxBikes) return;
      newBikeRows.push({ ...initialRow, uid: ++uid.current });
      bikeRows.current = newBikeRows;
      handleBikeRowChange();
    };

    const removeBike = (index: number) => {
      const newBikeRows = [...bikeRows.current];
      newBikeRows.splice(index, 1);
      bikeRows.current = newBikeRows;
      handleBikeRowChange();
    };

    const handleChange = ({ index, row }: { index: number; row: Row }) => {
      const newBikeRows = [...bikeRows.current];
      newBikeRows[index] = row;
      bikeRows.current = newBikeRows;
      handleBikeRowChange();
    };

    return (
      <div className="w-full">
        <div
          className={`mt-2 pt-4 w-full content-center grid ${
            extended ? "grid-cols-2 max-md:grid-cols-1" : "grid-cols-1"
          } gap-4 items-center`}
          // style={{ gridTemplateColumns: extended ? "1fr 1fr" : "1fr" }}
        >
          {bikeRows.current.map(({ price, uvp, uid }, index) => (
            <BikeInputRow
              maxBikePrice={maxBikePrice || Infinity}
              minBikePrice={minBikePrice || 0}
              key={"row" + uid}
              price={price}
              uvp={uvp}
              onChange={handleChange}
              onRemove={removeBike}
              uid={uid}
              index={index}
              removeAvailable={bikeRows.current.length > 1}
              isProduct={isProduct || false}
              extended={extended}
            />
          ))}
        </div>
        <div
          className={`mt-4 pt-4 ${
            extended ? "block" : "hidden"
          } border-t border-t-[--color-border]`}
        >
          {bikeRows.current.length < maxBikes && (
            <div
              className="flex justify-end items-center gap-2 text-md cursor-pointer"
              onClick={addBike}
            >
              <span className="opacity-50">{_t("addBike")}</span>
              <Button
                variant="primary"
                noPadding
                className="rounded-none aspect-square w-5 leading-none text-xs"
              >
                +
              </Button>
            </div>
          )}
        </div>

        <div
          className={`${
            maxBikes > 1 && bikeRows.current.length > 1 ? "" : "hidden"
          }`}
        >
          <div className={`py-4 text-right ${extended ? "block" : "hidden"}`}>
            {_t("sumBikes")}:{" "}
            {prices.current.total.toLocaleString("de-DE", {
              style: "currency",
              currency: "EUR",
            })}{" "}
            /{" "}
            {prices.current.totalUvp.toLocaleString("de-DE", {
              style: "currency",
              currency: "EUR",
            })}{" "}
            ({_t("UVP")})
          </div>
        </div>
      </div>
    );
  },
  (prevProps, nextProps) => {
    if (prevProps.extended !== nextProps.extended) return false;
    if (prevProps.sendParams !== nextProps.sendParams) return false;
    return true;
  }
);

export default BikeInput;
