import { useState } from "react";

interface SelectProps {
  options?: { name: string; optValue: string }[];
  disabled?: boolean;
  initialValue?: string;
  onChange?: (value: string) => void;
  type?: "number" | "bool" | "text";
  required?: boolean;
}

const Select = ({
  options = [],
  disabled,
  initialValue,
  onChange = () => {},
  type = "text",
  required = false,
}: SelectProps) => {
  const [value, setValue] = useState(initialValue);

  const handleChange = (e: { target: { value: string } }) => {
    const newValue =
      type === "number"
        ? parseFloat(e.target.value.toString())
        : type === "bool"
        ? e.target.value === "true"
          ? "true"
          : "false"
        : e.target.value;
    setValue(newValue.toString());
    onChange(newValue.toString());
  };
  return (
    <div
      className={`border border-[--color-border] w-full flex font-normal text-base `}
    >
      {options.length > 1 ? (
        <select
          onChange={handleChange}
          defaultValue={String(value)}
          className={`border-0 px-4 py-2 w-full border-r-8 border-r-transparent ${
            required && !value ? "bg-[--color-input-required]" : ""
          } ${
            disabled
              ? "cursor-not-allowed bg-[--color-input-disabled]"
              : "bg-[--color-input]"
          }`}
        >
          {options.map(({ name, optValue }) => {
            return (
              <option key={"key-" + optValue} value={optValue}>
                {name}
              </option>
            );
          })}
        </select>
      ) : (
        <p
          className={`color-black border-0 px-4 py-2 w-full ${
            disabled
              ? "cursor-not-allowed bg-[--color-input-disabled]"
              : "bg-[--color-input]"
          }`}
        >
          {options[0].name}
        </p>
      )}
    </div>
  );
};

export default Select;
