import getInsurancePackage from "./getInsurancePackage";
import { brutto } from "../../../utils/numbers";
import { InputParams, Settings } from "../../../defaultOptions";

export interface BikeLeasingInsurancesPerBike {
  leasinginsurancePerMonth: number;
  leasinginsurancePerMonthNet: number;
  leasingInsurance: number;
  leasingInsuranceNet: number;
  bikeWithInsurance: number;
}

interface LeasingInsurance {
  leasingRateInsuranceTotal: number;
  leasingRateInsuranceTotalNet: number;
  leasingRateInsurancePerMonth: number;
  leasingRateInsurancePerMonthNet: number;
  bikePriceWithLeasingInsurance: number;
  leasingRateInsurancesPerBike: BikeLeasingInsurancesPerBike[];
}

function getLeasingInsurance({
  inputParams,
  settings,
}: {
  inputParams: InputParams;
  settings: Settings;
}) {
  let leasingRateInsuranceTotal = 0,
    leasingRateInsuranceTotalNet = 0,
    bikePriceWithLeasingInsurance = 0;

  if (!inputParams?.bikeRows) return null;

  const leasingRateInsurancesPerBike = inputParams?.bikeRows.map(
    ({ price }: { price: number }) => {
      const { leasingInsurance, leasingInsuranceNet } =
        getLeasingInsuranceForBike({
          inputParams,
          settings,
          bikePrice: price,
        });
      leasingRateInsuranceTotal += leasingInsurance;
      leasingRateInsuranceTotalNet += leasingInsuranceNet;
      bikePriceWithLeasingInsurance += price + leasingInsurance;
      const leasinginsurancePerMonth =
        leasingInsurance / (inputParams?.leasingPeriod || 36);
      const leasinginsurancePerMonthNet =
        leasingInsuranceNet / (inputParams?.leasingPeriod || 36);
      return {
        leasinginsurancePerMonth,
        leasinginsurancePerMonthNet,
        leasingInsurance,
        leasingInsuranceNet,
        bikeWithInsurance: price + leasingInsurance,
      };
    }
  );

  const leasingRateInsurancePerMonth =
    leasingRateInsuranceTotal / (inputParams.leasingPeriod || 36);

  const leasingRateInsurancePerMonthNet =
    leasingRateInsuranceTotalNet / (inputParams.leasingPeriod || 36);

  const leasingInsurance: LeasingInsurance = {
    leasingRateInsuranceTotal,
    leasingRateInsuranceTotalNet,
    leasingRateInsurancePerMonth,
    leasingRateInsurancePerMonthNet,
    bikePriceWithLeasingInsurance,
    leasingRateInsurancesPerBike,
  };
  return leasingInsurance;
}

export default getLeasingInsurance;

function getLeasingInsuranceForBike({
  bikePrice,
  settings,
  inputParams,
}: {
  bikePrice: number;
  settings: Settings;
  inputParams: InputParams;
}) {
  const { leasingInsurancePrices } = getInsurancePackage({
    inputParams,
    settings,
  });
  const highestPrice =
    leasingInsurancePrices?.[leasingInsurancePrices.length - 1];
  const lowestPrice = leasingInsurancePrices?.[0];

  let leasingInsuranceNet: number = 0;

  if (lowestPrice?.upto) {
    leasingInsuranceNet = lowestPrice?.value || 0;
    if (bikePrice > lowestPrice?.upto && leasingInsurancePrices?.length) {
      for (let i = leasingInsurancePrices?.length - 1; i >= 0; i--) {
        const level = leasingInsurancePrices?.[i];
        if (level?.upto && bikePrice <= level?.upto)
          leasingInsuranceNet = level?.value || 0;
      }
    }
    if (highestPrice?.upto && bikePrice > highestPrice?.upto)
      leasingInsuranceNet = highestPrice.value || 0;
  }

  const inspectionCostsToExclude = 3.33 * 36;
  const insuranceWithoutInspection =
    leasingInsuranceNet - inspectionCostsToExclude;

  const leasingInsurance = settings?.noTaxOnInsurance
    ? insuranceWithoutInspection + brutto(inspectionCostsToExclude)
    : brutto(leasingInsuranceNet);

  return { leasingInsurance, leasingInsuranceNet };
}
