import getSalaryNetto from "./getSalaryNetto";
import getSavings, { SavingsOutput } from "./getSavings";
import getBikePrices from "./getBikePrices";
import getEmployerBenefit from "./getEmployerBenefit";
import { InputParams, Settings } from "../../../defaultOptions";
import { Comparison, Results } from "../Result";

export interface BMFCalculator {
  PKV: number;
  KRV: number;
  [key: string]: number | boolean | number[];
}

const calculate = ({
  settings,
  inputParams,
}: {
  settings: Settings;
  inputParams: InputParams;
}) => {
  let savings: SavingsOutput | undefined;
  let comparison: Comparison | undefined;

  if (!inputParams?.totalPriceUVP) return null;

  // LEASING perspective
  const bikePrices = getBikePrices({
    inputParams,
    settings,
  });

  if (!bikePrices) return null;

  // EMPLOYER BENEFIT
  const benefit = getEmployerBenefit({
    bikePrices,
    settings,
    inputParams,
  });

  if (inputParams?.salaryGross) {
    // NETTO WITHOUT BIKES
    const taxBasisWithoutBikes = inputParams?.salaryGross - benefit.other;
    const contributionsWithoutBikes = getSalaryNetto({
      inputParams,
      taxBasis: taxBasisWithoutBikes,
    });

    // NETTO WITH BIKES
    const salaryGross =
      inputParams?.salaryGross -
      bikePrices.leasingRate +
      benefit.other +
      benefit.bikes -
      bikePrices.bikePriceVatToSubtract;

    const taxBasisWithBikes = salaryGross + bikePrices.bikePricePercent;
    const contributionsWithBikes = getSalaryNetto({
      inputParams,
      taxBasis: taxBasisWithBikes,
    });

    const salaryNet =
      contributionsWithBikes.salaryNet - bikePrices.bikePricePercent; // Auszahlungsbetrag auf Konto

    // Leasingpreis per Gehaltsumwandlung
    const leasingRate =
      Math.round((contributionsWithoutBikes.salaryNet - salaryNet) * 100) / 100;

    const takeoverPrice = bikePrices.bikePrice * 0.1;
    const leasingTotal = leasingRate * (inputParams?.leasingPeriod || 36);
    const totalCost = leasingTotal + takeoverPrice;

    savings = getSavings({
      bikePrices,
      leasingRate,
      totalCost,
      leasingTotal,
      leasingPriceNormalTotal: bikePrices.leasingRateNormalTotal,
    });

    comparison = {
      withBikes: {
        contributions: contributionsWithBikes,
        taxBasis: taxBasisWithBikes,
        leasingRate,
        leasingTotal,
        salaryGross,
        salaryNet,
        takeoverPrice,
        totalCost,
      },
      withoutBikes: {
        contributions: contributionsWithoutBikes,
        taxBasis: taxBasisWithoutBikes,
        salaryGross: inputParams?.salaryGross,
        salaryNet: contributionsWithoutBikes.salaryNet,
      },
    };
  }

  const outputParams: Results = {
    inputParams,
    bikePrices,
    savings,
    settings,
    benefit,
    comparison,
  };
  return outputParams;
};

export default calculate;
