import { useEffect, useRef, useState } from "react";
import _t from "../../utils/translations";
import Row from "../common/Row";
import Box from "../common/Box";
import Label from "../common/Label";
import Select from "../common/Select";
import Input from "../common/Input";
import Hint from "../common/Hint";
import HintTable from "../common/HintTable";
import BikeInput from "../common/BikeInput";
import Toggle from "../common/Toggle";
import Wrap from "../common/Wrap";
import Section from "../common/Section";
import Result, { Results } from "./Result";
import Button from "../common/Button";
import calculate from "./calc/calculate";
import getEmployerBenefit from "./calc/getEmployerBenefit";
import { useOptions } from "../../utils/useOptions";
import { InputParams, Options } from "../../defaultOptions";
import Required from "../common/Required";

const validateOutput = (results: Results | null) => {
  if (!results?.comparison?.withBikes?.leasingRate) return false;
  else return true;
};

export default function Calculator() {
  const { options } = useOptions();
  const { initialParams, settings, hiddenInputs } = options as Options;
  const sendParams = useRef({ ...initialParams });
  const [results, setResults] = useState({} as Results | null);
  const [changed, setChanged] = useState({} as InputParams);
  const [extended, setExtended] = useState(
    settings?.formMode === "compact" ? false : true
  );
  const [error, setError] = useState(false);

  useEffect(() => {
    if (settings?.calculateMethod === "direct") {
      handleSubmit();
    }
  }, [sendParams.current]);

  if (!settings || !initialParams) return null;

  const {
    maxBikes,
    insurancePackages,
    benefitType,
    benefitFixed,
    regions,
    formMode,
    calculateMethod,
  } = settings;

  const { employerTaxDeduction, totalPrice, totalPriceUVP } = initialParams;

  const handleSubmit = () => {
    setError(false);
    const resultsCalculated = calculate({
      settings,
      inputParams: sendParams.current,
    });

    setResults(resultsCalculated);
    if (validateOutput(resultsCalculated)) {
      setChanged({});
    } else {
      if (!sendParams.current.salaryGross) return;
      if (!sendParams.current.region) return;
      setError(true);
    }
  };

  const setParams = (params: InputParams) => {
    const newSendParams = {
      ...initialParams,
      ...sendParams.current,
      ...params,
    };

    sendParams.current = newSendParams;

    if (calculateMethod === "direct") {
      handleSubmit();
    }

    setChanged(params);
  };

  const isVisible = (param: string) => hiddenInputs?.includes(param) === false;

  const benefit = !results
    ? { bikes: 0, internet: 0, phone: 0, ads: 0 }
    : getEmployerBenefit({
        inputParams: { ...settings, ...sendParams.current },
        bikePrices: results?.bikePrices,
        settings: settings,
      });

  return (
    <>
      <Wrap>
        <div className="border border-[--color-border] bg-[--color-head-box-bg] pb-6">
          <h1 className="px-10 py-8 bg-[--color-head-row-bg] text-2xl font-display border-b border-b-[--color-border]">
            {_t("bikes")}
          </h1>
          <Row className="px-10">
            <Box>
              <BikeInput
                extended={extended}
                onChange={(params) => setParams({ ...params })}
                maxBikes={maxBikes || Infinity}
                initialValue={totalPrice || 0}
                initialValueUvp={totalPriceUVP || 0}
                sendParams={sendParams.current}
              />
            </Box>
          </Row>
        </div>
        <Section className="pt-6">
          <Row>
            <Label>
              {sendParams?.current.workTypeOfficial
                ? _t("salaryGrossOfficial")
                : _t("salaryGross")}{" "}
              <Required />
            </Label>
            {_t("salaryGrossWithTax") !== "salaryGrossWithTax" && (
              <Hint>
                <p>{_t("salaryGrossWithTax")}</p>
              </Hint>
            )}
            <Box>
              <Input
                required
                onChange={(salaryGross) => setParams({ salaryGross })}
                type="number"
                min={0}
                placeholder={sendParams?.current?.salaryGross?.toString()}
                initialValue={sendParams?.current?.salaryGross || 0}
                prefix="€"
              />
            </Box>
          </Row>
        </Section>
        <Section>
          {isVisible("region") && (
            <Row>
              <Label>
                {_t("region")} <Required />
              </Label>
              <Box>
                <Select
                  required
                  onChange={(region) => setParams({ region })}
                  initialValue={sendParams?.current.region}
                  type="text"
                  options={[
                    { name: "bitte Bundesland wählen", optValue: "" },
                    ...(regions ?? []).map(({ full, short }) => {
                      return { name: full!, optValue: short! };
                    }),
                  ]}
                />
              </Box>
            </Row>
          )}
        </Section>

        {formMode === "compact" && (
          <Section>
            <Row className="justify-center py-2 border-t border-b mb-4">
              <Button
                className="text-sm"
                onClick={() => setExtended(!extended)}
                type="link"
              >
                {extended ? (
                  <>{_t("hideFields")} -</>
                ) : (
                  <>{_t("showFields")} +</>
                )}
              </Button>
            </Row>
          </Section>
        )}

        {(isVisible("insurancePackage") || isVisible("leasingPeriod")) && (
          <Section className={extended ? "block" : "hidden"}>
            {isVisible("insurancePackage") && (
              <Row>
                <Label>
                  {_t("insurancePackage")}{" "}
                  <Hint url={settings?.linkToInsurances}>
                    <HintTable></HintTable>
                  </Hint>
                </Label>
                <Box>
                  <Select
                    onChange={(insurancePackage) =>
                      setParams({ insurancePackage })
                    }
                    initialValue={sendParams?.current?.insurancePackage}
                    type="text"
                    options={insurancePackages?.map(
                      ({ name, title }: { name?: string; title?: string }) => {
                        return { name: title!, optValue: name! };
                      }
                    )}
                  />
                </Box>
              </Row>
            )}
            {isVisible("leasingPeriod") && (
              <Row>
                <Label>{_t("leasingPeriod")}</Label>
                <Box>
                  <Select
                    onChange={(leasingPeriod) =>
                      setParams({ leasingPeriod: parseFloat(leasingPeriod) })
                    }
                    initialValue={sendParams?.current?.leasingPeriod?.toString()}
                    type="number"
                    options={[
                      { name: "36", optValue: "36" },
                      { name: "48", optValue: "48" },
                    ]}
                  />
                </Box>
              </Row>
            )}
          </Section>
        )}

        {((employerTaxDeduction && isVisible("employerTaxDeduction")) ||
          (isVisible("benefit") && benefitType !== 0)) && (
          <Section className={extended ? "block" : "hidden"}>
            {isVisible("benefit") && (
              <>
                <Row>
                  <Label>
                    {benefitType === 2 ? _t("benefitPerBike") : _t("benefit")}
                    <Hint>
                      <h2>{_t("benefitHintHeadline")}</h2>
                      <p>{_t("benefitHintText")}</p>
                    </Hint>
                  </Label>
                  <Box>
                    {settings?.benefitType === 7 &&
                    settings?.benefitDropdown &&
                    settings.benefitDropdown.length > 0 ? (
                      <Select
                        onChange={(benefit) =>
                          setParams({ benefit: parseFloat(benefit) })
                        }
                        initialValue={sendParams?.current?.benefit?.toString()}
                        options={settings.benefitDropdown}
                      />
                    ) : (
                      <Input
                        onChange={(benefit) => setParams({ benefit })}
                        initialValue={benefit.bikes}
                        calculatedValue={benefit.bikes}
                        disabled={benefitFixed}
                        step={0.1}
                        min={0}
                        prefix={"€"}
                        type="number"
                      />
                    )}
                  </Box>
                </Row>

                {settings?.benefitAdditional &&
                  settings?.benefitAdditional.length > 0 &&
                  settings?.benefitAdditional.map(
                    ({ name, value, editable }, index) => (
                      <Row key={index}>
                        <Label>{name}</Label>
                        <Box>
                          <Input
                            onChange={(value) => {
                              const benefitAdditional =
                                sendParams?.current?.benefitAdditional ||
                                settings?.benefitAdditional ||
                                [];
                              benefitAdditional[index] = { name, value };
                              setParams({ benefitAdditional });
                            }}
                            initialValue={
                              sendParams?.current?.benefitAdditional?.[index]
                                ?.value ||
                              value ||
                              0
                            }
                            disabled={!editable}
                            step={0.1}
                            prefix="€"
                            min={0}
                            type="number"
                          />
                        </Box>
                      </Row>
                    )
                  )}
              </>
            )}

            {isVisible("benefitInternet") && (
              <Row>
                <Label>{_t("benefitInternet")}</Label>
                <Box>
                  <Input
                    onChange={(benefitInternet) =>
                      setParams({ benefitInternet })
                    }
                    initialValue={sendParams?.current?.benefitInternet || 0}
                    step={0.1}
                    prefix="€"
                    min={0}
                    type="number"
                  />
                </Box>
              </Row>
            )}

            {isVisible("benefitPhone") && (
              <Row>
                <Label>{_t("benefitPhone")}</Label>
                <Box>
                  <Input
                    onChange={(benefitPhone) => setParams({ benefitPhone })}
                    initialValue={sendParams?.current?.benefitPhone || 0}
                    step={0.1}
                    prefix="€"
                    min={0}
                    type="number"
                  />
                </Box>
              </Row>
            )}

            {isVisible("benefitAds") && (
              <Row>
                <Label>{_t("benefitAds")}</Label>
                <Box>
                  <Input
                    onChange={(benefitAds) => setParams({ benefitAds })}
                    initialValue={sendParams?.current?.benefitAds || 0}
                    step={0.1}
                    prefix="€"
                    min={0}
                    type="number"
                  />
                </Box>
              </Row>
            )}

            {employerTaxDeduction && isVisible("employerTaxDeduction") && (
              <Row>
                <Label>
                  {_t("employerTaxDeduction")}
                  <Hint>
                    <h2>{_t("employerTaxDeductionHintHeadline")}</h2>
                    <p>{_t("employerTaxDeductionHintText")}</p>
                  </Hint>
                </Label>
                <Box>
                  <Toggle
                    onChange={(employerTaxDeduction) =>
                      setParams({ employerTaxDeduction })
                    }
                    initialValue={sendParams?.current.employerTaxDeduction}
                  />
                </Box>
              </Row>
            )}
          </Section>
        )}

        <Section className={extended ? "block" : "hidden"}>
          {isVisible("taxClass") && (
            <Row>
              <Label>{_t("taxClass")}</Label>
              <Box>
                <Select
                  onChange={(taxClass) =>
                    setParams({ taxClass: parseInt(taxClass) })
                  }
                  initialValue={sendParams?.current?.taxClass?.toString()}
                  type="number"
                  options={[
                    { optValue: "1", name: "Klasse I" },
                    { optValue: "2", name: "Klasse II" },
                    { optValue: "3", name: "Klasse III" },
                    { optValue: "4", name: "Klasse IV ohne Faktorverfahren" },
                    { optValue: "5", name: "Klasse IV mit Faktorverfahren" },
                    { optValue: "6", name: "Klasse V" },
                    { optValue: "7", name: "Klasse VI" },
                  ]}
                />
              </Box>
            </Row>
          )}
          {
            <Row
              className={`${
                sendParams?.current.taxClass === 5 ? "flex" : "hidden"
              }`}
            >
              <Label>{_t("taxFactor")}</Label>
              <Box>
                <Input
                  onChange={(taxFactor) => setParams({ taxFactor })}
                  initialValue={sendParams?.current.taxFactor || 1}
                  step={0.001}
                  type="number"
                  max={1}
                  min={0.001}
                />
              </Box>
            </Row>
          }
          {isVisible("churchTax") && (
            <Row>
              <Label>{_t("churchTax")}</Label>
              <Box>
                <Toggle
                  onChange={(churchTax) => setParams({ churchTax })}
                  initialValue={sendParams?.current.churchTax}
                />
              </Box>
            </Row>
          )}
          {/*               
              TODO: 
              Kinder
          */}

          {isVisible("hasChildren") && (
            <>
              <Row>
                <Label>{_t("hasChildren")}</Label>
                <Box>
                  <Toggle
                    onChange={(hasChildren) => setParams({ hasChildren })}
                    initialValue={sendParams?.current.hasChildren}
                  />
                </Box>
              </Row>
              {sendParams?.current.hasChildren && (
                <>
                  <Row>
                    <Label>{_t("childAllowances")}</Label>
                    <Box>
                      <Input
                        onChange={(childAllowances) =>
                          setParams({ childAllowances })
                        }
                        initialValue={sendParams?.current?.childAllowances || 0}
                        step={0.5}
                        type="number"
                      />
                    </Box>
                  </Row>
                  <Row>
                    <Label>{_t("childrenCount")}</Label>
                    <Box>
                      <Input
                        onChange={(childrenCount) =>
                          setParams({ childrenCount })
                        }
                        initialValue={sendParams?.current?.childrenCount || 0}
                        step={1}
                        type="number"
                      />
                    </Box>
                  </Row>
                </>
              )}
            </>
          )}
        </Section>
        {(isVisible("healthInsurance") ||
          isVisible("pensionInsurance") ||
          isVisible("workTypeOfficial")) && (
          <Section className={extended ? "block" : "hidden"}>
            {isVisible("workTypeOfficial") && (
              <Row>
                <Label>{_t("workTypeOfficial")}</Label>
                <Box>
                  <Toggle
                    onChange={(workTypeOfficial) =>
                      setParams({ workTypeOfficial })
                    }
                    initialValue={sendParams?.current.workTypeOfficial}
                  />
                </Box>
              </Row>
            )}
            {!sendParams?.current?.workTypeOfficial &&
              isVisible("healthInsurance") && (
                <>
                  <Row>
                    <Label>{_t("healthInsurance")}</Label>
                    <Box>
                      <Select
                        onChange={(healthInsurance) =>
                          setParams({
                            healthInsurance: parseInt(healthInsurance),
                          })
                        }
                        initialValue={
                          sendParams?.current.healthInsurance?.toString() || "1"
                        }
                        type="number"
                        options={[
                          {
                            optValue: "1",
                            name: "gesetzliche Krankenversicherung",
                          },
                          {
                            optValue: "2",
                            name: "private Krankenversicherung",
                          },
                          // {
                          //   optValue: "2",
                          //   name: "private Krankenversicherung ohne Arbeitgeberzuschuss",
                          // },
                          // {
                          //   optValue: "3",
                          //   name: "private Krankenversicherung mit Arbeitgeberzuschuss",
                          // },
                        ]}
                      />
                    </Box>
                  </Row>

                  <Row
                    className={`${
                      sendParams?.current.healthInsurance === 1
                        ? "flex"
                        : "hidden"
                    }`}
                  >
                    <Label>
                      {_t("healthInsuranceFactor")}
                      <Hint>
                        <h2>{_t("healthInsuranceFactorHintHeadline")}</h2>
                        <p>{_t("healthInsuranceFactorHintText")}</p>
                      </Hint>
                    </Label>
                    <Box>
                      <Input
                        onChange={(healthInsuranceFactor) =>
                          setParams({ healthInsuranceFactor })
                        }
                        initialValue={
                          sendParams?.current.healthInsuranceFactor || 1.6
                        }
                        step={0.01}
                        min={0.01}
                        max={10}
                        prefix="%"
                        type="number"
                      />
                    </Box>
                  </Row>
                </>
              )}

            {!sendParams?.current?.workTypeOfficial &&
              isVisible("pensionInsurance") && (
                <Row>
                  <Label>{_t("pensionInsurance")}</Label>
                  <Box>
                    <Select
                      onChange={(pensionInsurance) =>
                        setParams({
                          pensionInsurance: parseInt(pensionInsurance),
                        })
                      }
                      initialValue={
                        sendParams?.current.pensionInsurance?.toString() || "0"
                      }
                      type="number"
                      options={[
                        { name: "Ja (West)", optValue: "0" },
                        { name: "Ja (Ost)", optValue: "1" },
                        { name: "Nein", optValue: "2" },
                      ]}
                    />
                  </Box>
                </Row>
              )}
          </Section>
        )}
        <Section noBorder>
          <Row
            className={`${
              changed?.bikeRows && calculateMethod !== "direct" ? "" : "hidden"
            }`}
          >
            <Label></Label>
            <Box>
              <Button size="big" onClick={handleSubmit} className="w-full">
                {_t("calculate")}
              </Button>
            </Box>
          </Row>
          <Row>
            <Box className="justify-center w-full flex-col">
              {/* <ErrorMessage error={error} /> */}
            </Box>
          </Row>
        </Section>
        {/* {results?.bikePrices && ( */}
        <Result extended={extended} results={{ ...results }} />
        {/* )} */}
        <div
          className={`border-2 border-[#cf0000] p-4 rounded-md ${
            error ? "" : "hidden"
          }`}
        >
          {_t("error")}
        </div>
      </Wrap>
    </>
  );
}
