import _t from "../../../utils/translations";
import { formatEur } from "../../../utils/numbers";
import { BikePrices } from "../calc/getBikePrices";

const LeasingCosts = ({ bikePrices }: { bikePrices?: BikePrices }) => (
  <div>
    <h3 className="pr-8">{_t("calculationLeasingCosts")}</h3>
    <div>
      <table className="[&_td]:text-right">
        <tbody>
          <tr className="hightlight">
            <th>
              {_t("leasingRate")} {_t("leasingRateAddition")}
            </th>
            <td>
              {bikePrices &&
                formatEur(
                  bikePrices?.leasingRate -
                    bikePrices?.leasingRateInsurancePerMonth
                )}
            </td>
          </tr>
          {bikePrices?.leasingRateInsurancesPerBike?.map(
            (
              {
                leasinginsurancePerMonth,
              }: { leasinginsurancePerMonth: number },
              index: number
            ) => (
              <tr className="hightlight" key={index + leasinginsurancePerMonth}>
                <th>
                  {_t("leasinginsurancePerMonth")} {_t("forBike")} #{index + 1}
                </th>
                <td>{formatEur(leasinginsurancePerMonth)}</td>
              </tr>
            )
          )}
        </tbody>
        <tfoot>
          <tr className="">
            <th>{_t("leasingPriceTotal")}</th>
            <th className="text-right">
              <span className="font-normal">{_t("exclVat")}</span>{" "}
              {formatEur(bikePrices?.leasingRate)}
            </th>
          </tr>
        </tfoot>
      </table>
    </div>
  </div>
);

export default LeasingCosts;
