import { netto } from "../../../utils/numbers";
import { InputParams, Settings } from "../../../defaultOptions";
import getInsurancePackage from "./getInsurancePackage";

interface LeasingFactorParams {
  settings: Settings;
  inputParams: InputParams;
  bikePrice: number;
  bikePriceWithLeasingInsurance: number;
}

interface LeasingFactor {
  leasingRateFactor: number;
}

function getLeasingFactor({
  settings,
  inputParams,
  bikePrice,
  bikePriceWithLeasingInsurance,
}: LeasingFactorParams) {
  let factor;
  let priceToCompareWith = netto(bikePrice);
  const { leasingFactors } = getInsurancePackage({ inputParams, settings });

  const highestFactor = leasingFactors?.[leasingFactors.length - 1];
  const lowestFactor = leasingFactors?.[0];
  factor = lowestFactor?.value || 3.22;

  if (settings?.includeInsuranceForFactorComparison) {
    priceToCompareWith = netto(bikePriceWithLeasingInsurance);
  }

  if (
    priceToCompareWith > (lowestFactor?.upto || Infinity) &&
    leasingFactors?.length
  ) {
    for (let i = leasingFactors?.length - 1; i >= 0; i--) {
      const level = leasingFactors?.[i];
      if (level?.upto && priceToCompareWith <= level?.upto)
        factor = level?.value;
    }
  }

  if (highestFactor?.upto && priceToCompareWith > highestFactor?.upto)
    factor = highestFactor?.value;

  const leasingFactor: LeasingFactor = {
    leasingRateFactor: factor || 3.22,
  };

  return leasingFactor;
}

export default getLeasingFactor;
